@charset "UTF-8";
@import "../css/normalize.css";
@font-face {
  font-family: "Comfortaa";
  src: local("Comfortaa Regular"), local("Comfortaa-Regular"), url("../fonts/Comfortaa/Comfortaa-Regular.woff") format("woff"), url("../fonts/Comfortaa/Comfortaa-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  src: local("Comfortaa Bold"), local("Comfortaa-Bold"), url("../fonts/Comfortaa/Comfortaa-Bold.woff") format("woff"), url("../fonts/Comfortaa/Comfortaa-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: local("Poppins Semibold"), local("Poppins-Semibold"), url("../fonts/Poppins/Poppins-Semibold.woff") format("woff"), url("../fonts/Poppins/Poppins-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  height: 100%;
}

.page__body {
  opacity: 0;
  transition: 0.9s opacity ease-in-out;
  margin: 0;
  font-family: "Comfortaa", sans-serif;
  font-weight: normal;
  color: #171717;
  position: relative;
  overflow-x: hidden;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
.page__body::before {
  position: absolute;
  content: "";
  width: rem(26px);
  height: 100%;
  background: url("../img/decoration-left.svg") repeat-y;
  left: 0;
  top: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .page__body::before {
    width: rem(32px);
    left: rem(-11px);
    background: url("../img/decoration-left_tablet.svg") repeat-y;
  }
}
@media (min-width: 1260px) {
  .page__body::before {
    width: rem(123px);
    left: rem(-16px);
    background: url("../img/decoration-left_desktop.svg") repeat-y;
  }
}
.page__body::after {
  position: absolute;
  content: "";
  width: rem(25px);
  height: 100%;
  background: url("../img/decoration-right.svg") repeat-y;
  right: 0;
  top: 0;
  z-index: 1;
}
@media (min-width: 768px) {
  .page__body::after {
    width: rem(32px);
    background: url("../img/decoration-right_tablet.svg") repeat-y;
  }
}
@media (min-width: 1260px) {
  .page__body::after {
    width: rem(146px);
    right: rem(-36px);
    background: url("../img/decoration-right_desktop.svg") repeat-y;
  }
}
.page__body--visible {
  opacity: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: rem(0 20px);
}

@media (min-width: 768px) {
  .container {
    padding: rem(0 30px);
  }
}
@media (min-width: 1260px) {
  .container {
    padding: rem(0 110px);
  }
}
.color {
  color: #ff498b;
  font-weight: bold;
}
.color span {
  position: relative;
}
.color span::after {
  content: "´";
  position: absolute;
  top: rem(-2px);
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .color span::after {
    top: rem(-6px);
  }
}
@media (min-width: 1260px) {
  .color span::after {
    top: rem(-9px);
  }
}

