.example__heading {
  margin: rem(0 0 15px 0);
  padding: 0;
  font-weight: bold;
  font-size: rem(35px);
  line-height: 1;
  text-align: center;
}

.example__paragraph {
  display: block;
  margin: 0;
  padding: 0;
  font-size: rem(22px);
  line-height: rem(33px);
  text-align: center;
}

.example__button {
  display: block;
