  transition: all 0.3s ease-in;
}

.test__paragraph {
  text-align: center;
  width: 68%;
  margin: rem(0 auto 25px);
}

.test__list {
  margin: rem(0 0 28px);
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: rem(17px);
}

.test__button {
  background: #cfcdff;
  border: none;
  margin: rem(0 auto 10px);
  padding: rem(11px);
  border-radius: rem(7px);
  width: 76%;
  color: #171717;
  font-size: rem(18px);
  line-height: rem(24px);
  display: flex;
  justify-content: center;
  transition: background-color ease 0.3s;
  cursor: pointer;
}
@media (min-width: 768px) {
  .test__button {
    width: 100%;
  }
}
.test__button:hover {
  background-color: #e7e7ff;
}
.test__button--wrong {
  background: #eb6465;
}
.test__button--wrong:hover {
  background-color: #ee7b7b;
}
.test__button--correct {
  background: #b7ff8d;
}
.test__button--correct:hover {
  background-color: #c7ffa7;
}
.test__button--passive {
  background: #e4e4e4;
}
.test__button--passive:hover {
  background-color: #f1f1f1;
}

@media (min-width: 768px) {
