    padding: 10px 0;
  }
}
.morphemic-parsing__table span {
  text-align: center;
  margin: 0 auto;
  display: block;
}
.morphemic-parsing__table input {
  height: 30px;
  width: 60px;
  border-radius: 10px;
  border: 2px solid #9390fc;
  display: block;
  margin: 0 auto;
  font: inherit;
  padding: 0 10px;
}
