  border: 2px solid #0800ff;
}
.morphemic-parsing__table input.right {
  border-color: #97ff5a;
  background: #ceffb1;
}
.morphemic-parsing__table input.wrong {
  background: #ffcdcd;
  border-color: #ff6464;
}

.morphemic-parsing__paragraph {
