  }
}

.morphemic-parsing__button {
  border: none;
  background: #cfcdff;
  width: 40%;
  margin: 0 auto;
  display: block;
  font-size: 18px;
  line-height: 24px;
  padding: 11px 0;
  border-radius: 5px;
  cursor: pointer;
}
.morphemic-parsing__button:hover {
  transition: 0.3s all ease-in;
  background: #b7b4ff;
}
.morphemic-parsing__button:disabled {
  display: none;
}

