  display: none;
  position: fixed;
  bottom: rem(120px);
  left: 0;
  right: 0;
  height: 70vh;
  margin: rem(0 20px);
  border-radius: rem(15px);
  background: #ffffff;
  border: rem(3px) solid #ff498b;
  box-shadow: 0 0 30px 5px rgba(0, 0, 0, 0.4);
}

@keyframes animateBorder {
  0% {
    border: rem(2px) solid #ff498b;
  }
  100% {
    border: rem(2px) solid white;
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 73, 139, 0.4), 0 0 0 2px rgba(255, 73, 139, 0.2), 0 0 0 4px rgba(255, 73, 139, 0.1);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(255, 73, 139, 0.8), 0 0 0 15px rgba(255, 73, 139, 0.6), 0 0 0 20px rgba(255, 73, 139, 0.3);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 73, 139, 0.4), 0 0 0 2px rgba(255, 73, 139, 0.2), 0 0 0 4px rgba(255, 73, 139, 0.1);
  }
}
@keyframes animateIconPeople {
  0% {
    transform: translate(-50%, -50%);
  }
  100% {
    transform: translate(rem(-100px), -50%);
  }
}
@keyframes animateIconClose {
  0% {
    transform: translate(rem(50px), -50%);
  }
  100% {
    transform: translate(-50%, -50%);
  }
}